import React, { useState, useEffect, useRef } from 'react';
import { FaStar } from 'react-icons/fa'; 
import "../Page/Css/CommentInput.css";



const ARRAY = [0, 1, 2, 3, 4];

function CommentInput() {

  const week = ["", "", "", "", "", "", ""];
  const today = new Date();
  today.setDate(today.getDate() );
  const time = {
    year: today.getFullYear(), // 
    month: today.getMonth() + 1, //  
    date: today.getDate(), //  
  };
  const dayOfWeek = week[today.getDay()];
  const clickdate = `${time.year} / ${time.month} / ${time.date} / ${dayOfWeek} `;


  const [note, setNote] = useState('');
  const [username, setusername] = useState('');
  const [lists, setLists] = useState([]);
  const [nextId, setNextId] = useState(0);

  const inputusername = useRef(null);
  const inputnote = useRef(null);


/* input        */
const onChange = (e) => {
  setNote(e.target.value);
};
const onChange2 = (e) => {
  setusername(e.target.value);
};

const submit = (e) => {
  e.preventDefault();  // 
  const about_lists = lists.concat({ //   
    id: nextId,
    name: username,
    text: note,
    star: sendReview(),
    date : today
  });
  setNextId(nextId + 1); //id +1

  /*      */
  setLists(about_lists); 
  setNote('');
  setusername(''); //input    
};

/*     map    */

    
console.log(note);
console.log(username);




const [clicked, setClicked] = useState([false, false, false, false, false]);

const handleStarClick = index => {
  let clickStates = [...clicked];
  for (let i = 0; i < 5; i++) {
    clickStates[i] = i <= index ? true : false;
  }
  setClicked(clickStates);
};

useEffect(() => {
  sendReview();
}, [clicked]); // 

const sendReview = () => {
  let score = clicked.filter(Boolean).length;
   let reviewnote = ""; 
  switch (score) {
      case 1 : 
      return reviewnote = "1   "
      case 2 : 
      return reviewnote = "2 "
      case 3 : 
      return reviewnote = "3 "
      case 4 : 
      return reviewnote = "4 "
      case 5 : 
      return reviewnote = "5 "
  }
};


const input_list = lists.map((list) => (
  <li key={list.id} >
  <div  id="wrtinb">{list.name} :  {list.text}  </div>  <div id="wrtin"> : {clickdate} <br></br>  </div> 
  <div> {list.star} </div>
  <div>  <button  onClick={() => removeList(list.id)} > X </button> </div>
  </li>
  
));

//  
const removeList = (id) => {
  const about_lists = lists.filter((list) => list.id !== id);
  setLists(about_lists);
};


  return (<div>

<form onSubmit={submit}>
    <label></label>
    <input name = "username"  type="text"  placeholder=" "           value={username}
         classname="textbosss" onChange={onChange2} ref={inputusername} id="namebox" 
         style={{ width: "50%"}} required />
<br></br>

<div className='Wrap'>

      <div className='Stars'>
        {ARRAY.map((el, idx) => {
          return (
            <FaStar
              key={idx}
              size="50"
              onClick={() => handleStarClick(el)}
              className={clicked[el] && 'yellowStar'}
            />
          );
        })}    <div className='RatingText'> <p> {sendReview() }</p>  </div>
      </div>
    </div>


<label></label>

         <input name="list"   type="text"  placeholder=" ."
          value={note}  onChange={onChange}  ref={inputnote} id="textbox"
           className='name_box' style={{ height: "100px"}} required />
    <br></br>
        <button type="submit"></button>
      </form>



      <hr></hr> <div className='inputlistun'>
      <ul>{input_list}</ul> </div>


</div>
  );

  }

export default CommentInput;

